import {useTranslation} from "react-i18next";


export default function Filtres() {
  const {t} = useTranslation('common');
  return (
    <div className="filters-body">
        <div className="filter-block"><div className="filters-title">{t('Location')}</div>
        <div className="filter-inputs">
           <form className="nosubmit">
          <input className="nosubmit sm:w-[306px] lg:w-[455px]" type="search" placeholder=", . " />
           </form>
        </div>
        </div>
        <div className="filter-block"><div className="filters-title"></div>
        <label className="main">{t('Static')}<input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main">{t('Dynamic')}<input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main">{t('Monitor')}<input type="checkbox" /><span className="geekmark"></span></label>
        </div>
        <div className="filter-block"><div className="filters-title">{t('Lighting at night')}</div>
        <label className="main">{t('Yes')}<input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main">{t('No')}<input type="checkbox" /><span className="geekmark"></span></label>
        </div>
        <div className="filter-block"><div className="filters-title">{t('Construction')}</div>
        <label className="main"><input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main"><input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main"><input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main"><input type="checkbox" /><span className="geekmark"></span></label>
        </div>
        <div className="filter-block"><div className="filters-title"></div>
        <div className="filter-inputs"> <input placeholder="2" />  <img src="./img/x.svg" alt="" />  <input placeholder="2" /> </div>
        <label className="main"> <input type="checkbox" /><span className="geekmark"></span></label>
        <label className="main"> <input type="checkbox" /><span className="geekmark"></span></label>
        </div>
        <div className="filter-block"><div className="filters-title"></div>
        <div className="filter-inputs"><input placeholder="0" />  <input placeholder="0" />   </div>
        </div>
        <button className="save-button">{t('Save')}</button>
        </div>
  )
}
