import { Link } from 'react-router-dom';

const NotFound = () => {
    return (
        <main>
            <div className="notfound">
                <p>!</p>
                <img src="./img/404.png" className="w-[250px] lg:w-[451px]"  alt="" />
                <p>  </p>
                <Link to="/"><button className="button"> </button></Link>
             </div>
        </main>
    )
}

export {NotFound}